<template>
  <nav class="top-bar row">
    <div class="title-area columns small-5 medium-3 large-2">
      <div class="icon__group">
        <router-link to="/">
          <img :src="logoImg" alt="Magnet ventures">
        </router-link>
      </div>
    </div>
    <div @mouseleave="onLeaveHeader" @click="onClick" href="#" class="title__menu columns small-7 medium-9 large-10">
      <img class="title__hamburger" :src="hamburgerImage" alt="">
      <ul class="nav--level-1-menu">
        <li @mouseenter="onhover" @mouseleave="onleave" v-for="menuItem in menu" class="nav--menu-item" :data-key="menuItem.key" :data-second-level="!!menuItem.subMenu">
          <router-link :to="`/${menuItem.key.toLowerCase()}`" class="nav__menu-alink">{{menuItem.key}}</router-link>
          <ul v-if="!!menuItem.subMenu" class="nav--level-2-menu">
            <li v-for="menuItem2 in menuItem.subMenu" class="nav--menu-item" :data-key="menuItem2.key">
              <router-link :to="menuItem2.url">{{menuItem2.key}}</router-link>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import HamburgerImage from '../assets/hamburger-menu.png';
import logoImg from '../assets/logo-min.jpg';

export default {
  name: 'NavBar',
  props: {
    hamburgerImage: {
      type: String,
      default: HamburgerImage
    },
    logoImg: {
      type: String,
      default: logoImg
    }
  },
  data() {
    return {
      checkedKey: null,
      checkedKey2: null,
      secondLevel: false,
      menu: [
        {
          key: 'PHILOSOPHY'
        },
        {
          key: 'TEAM'
        },
        {
          key: 'PORTFOLIO',
          subMenu: [
            {
              key: 'INNFOS',
              url: '/portfolio/innfos'
            }
          ]
        },
        {
          key: 'CONTACT'
        }
      ],
    }
  },
  methods: {
    onhover(e) {
      e.stopPropagation();
      const windowWidth = window.innerWidth;
      if (e.target.className.indexOf('active') < 0) {
        e.target.className += ' active';
      }
      if (e.target.dataset.key === 'PORTFOLIO') {
        if (windowWidth <= 480) {
          e.target.style.height = '110px';
        }
      }
    },
    onleave(e) {
      console.log('leave', e);
      e.stopPropagation();
      if (e.target.className.indexOf('active') >= 0) {
        e.target.className = e.target.className.replace(' active', '');
      }
      if (e.target.dataset.key === 'PORTFOLIO') {
        e.target.style.height = '55px';
      }
    },
    onClick(e) {
      e.stopPropagation();
      if (e.target.className.indexOf('title__hamburger') >= 0) {
        if (e.currentTarget.className.indexOf('active') < 0) {
          e.currentTarget.className += ' active';
        } else {
          e.currentTarget.className = e.currentTarget.className.replace(' active', '');
        }
      }
    },
    onLeaveHeader(e) {
      e.stopPropagation();
      e.target.className = e.target.className.replace(' active', '');
    }
  }
}
</script>

<style lang="scss">
@import '../App.scss';

@media screen and (max-width: 639px) {
  .top-bar.row {
    align-items: flex-start;
    flex-direction: column;
    .title__menu {
      align-self: stretch;
      max-width: 100%;
      padding: 0;
      &.active {
        .nav--level-1-menu {
          display: block;
        }
      }
    }
    .nav--level-1-menu {
      display: none;
      padding: 0;
      text-align: center;
      width: 100%;
      &>.nav--menu-item {
        background: rgb(37, 37, 37) !important;
      }
    }
    .nav--menu-item {
      height: rem-calc(55);
      line-height: rem-calc(55);
      display: block;
    }
    .title__hamburger {
      position: absolute;
      top: rem-calc(16);
      right: rem-calc(10);
      display: block;
      width: rem-calc(30);
      height: rem-calc(23);
    }
  }
}

.top-bar {
  background-color: rgb(35, 36, 37);
  &.row {
    max-width: 100%;
    position: fixed;
    top: 0;
    z-index: 10;
    width: 100%;
  }
  .title__hamburger {
    display: none;
  }
  .title__menu {
    display: flex;
    justify-content: flex-end;
  }
  ul {
    list-style-type: none;
    margin-left: 0;
  }
  a {
    &,
    &:hover,
    &:focus,
    &:active {
      color: white;
    }
  }
  .title-area {
    padding: rem-calc(5) rem-calc(20);
    color: white;
    h3 {
      margin-bottom: rem-calc(-10);
    }
    span {
      font-size: rem-calc(12);
    }
  }
  .icon__group {
    width: rem-calc(135);
    float: left;
  }
  .nav--level-1-menu {
    margin-bottom: 0;
  }
  .nav--level-2-menu {
    display: none;
    position: absolute;
    top: rem-calc(62);
    left: rem-calc(-40);
    height: rem-calc(40);
    line-height: rem-calc(40);
    width: rem-calc(160);
    color: white;
    background: red;
    &:before {
      content: "";  /*:before和:after必带技能，重要性为满5颗星*/
      display: block;
      position: absolute;  /*日常绝对定位*/
      background: red;
      top: rem-calc(-20);
      width: 0;
      height: 0;
      border: rem-calc(10) solid transparent;
      left: rem-calc(70);
      border-left-color: rgb(35, 36, 37);
      border-right-color: rgb(35, 36, 37);
      border-top-color: rgb(35, 36, 37);
    }

    .nav--menu-item {
      padding: 0;
      a {
        display: inline-block;
        width: 100%;
      }
      &.active,
      .router-link-active {
        color: #fff;
      }
    }
  }
  .nav--menu-item {
    position: relative;
    display: inline-block;
    font-family: font858;
    font-size: rem-calc(14);
    padding: rem-calc(20) rem-calc(5);
    height: 100%;
    @media screen and (max-width: 480px) {
      padding: 0;
    }
    &.active {
      ul {
        display: block;
        @media screen and (max-width: 639px) {
          display: block;
          position: static;
          float: left;
          width: 100%;
          height: 55px;
          &:before {
            display: none;
          }
        }
      }
      &>a {
        color: red;
      }
    }
    .router-link-active {
      color: red;
    }
    .nav__menu-alink {
      padding: rem-calc(26) 0;
      @media screen and (max-width: 480px) {
        padding: 0;
        line-height: rem-calc(55);
        width: 100%;
        display: inline-block
      }
    }
  }
}
</style>


